.cartContainer {
  display: flex;
  flex-wrap: wrap;
}

.itemsContainer {
  flex: 2;
}

.summaryContainer {
  flex: 1;
}

.border {
  border: 3px solid;
}

.summaryContainer {
  border-color: blue;
}

/* Responsive Styles */

@media screen and (max-width: 768px) {
  .cartContainer {
    flex-direction: column;
  }
  
  .itemsContainer,
  .summaryContainer {
    flex: 1;
    width: 100%;
  }
}


