.itemContainer {
  background-color: rgba(187, 187, 187, 0.305);
  display: flex;
  justify-content: space-between;
  width: 290%;
  max-width: 700px;
  align-items: center;
  margin: 0.5rem;
}

.imageContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 150px;
  width: 150px;
  margin-left: 3rem;
  padding: auto;
  max-height: 200px;
  max-width: 100%;
  overflow: hidden;
}

.itemImage {
  border-radius: 8px;
  max-width: 100%;
  max-height: 100%;
}

.amountContainer {
  display: flex;
  align-items: center;
}

.decrementButton {
  margin-right: 0.5rem;
  max-height:2rem ;
}

.incrementButton {
  margin-left: 0.5rem;
  max-height:2rem ;
}

.icon{
  margin-left: 0.5rem;
  margin-right: 0.5rem;
}

.border {
  border: 3px solid;
}
