.main {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid red;
}

.container {
  background-color: rgba(0, 0, 0, 0.047);
  border: 1px solid rgba(0, 0, 0, 0.423);
  display: flex;
  align-self: start;
  margin: 4rem;
  padding: 1.5rem;
}

.h2 {
  margin-top: 1.5rem;
  font-size: 1.2rem;
}

.group {
  margin: 1rem;
}

.image {
  border: 1px solid;
  border-radius: 5rem;
  height: 170px;
  width: 170px;
  max-height: 200px;
  max-width: 100%;
}

@media (max-width: 600px) {
  .container {
    width: 70%;
    margin: 2rem;
    padding: 1rem;
  }

  .image {
    height: 120px;
    width: 120px;
  }
}

@media (max-width: 650px) {
  .container {
    width: 90%;
    margin: 1rem;
    padding: 0.5rem;

    

    display: flex;
    flex-direction: column;
    align-items: center;
    
    
  }
 

  .image {
    height: 80px;
    width: 80px;
  }
}
